<template>
    <div class="overlay-content" style="border: solid">
        <!-- <p @click="click('닫기')">닫기</p> -->
        <label for="cars">Response 선택</label>
        <select v-model="response">
            <option v-for="(item, index) in data.rows" v-bind:key="index" >{{item}}</option>
        </select>
        <br>
        <input type="button" style="width:100%" @click="click('등록')" value="등록">
    </div>
</template>
<style scoped>
input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
</style>

<script>
import responseType from '../project/responseType';
import builderType from '../dictionary/builderType';
// import intentType from '../intentType';
import common from '../utils/common';

export default {
    name: 'ResponseSelect', //SKelton:DefineComponentName
    props: {
        data: Object,
    },
    data: function(){
        return {
            response: '',
        }
    },
    mounted() {
        console.log('ServiceSelect.mounted', this.data);
    },    
    computed: {
    },
    filters: {
    },
    methods: {
        click(type){
            switch (type) {
                case '등록':
                    {
                        console.log('response>', this.response);
                        const data = {
                            action: builderType.CONNECT_INTENT_RESPONSE,
                            intent: this.data.intent,
                            response: this.response,
                        }                  
                        console.log('data', data);
                        common.callResponse(responseType.MGBUILDER, data);
                    }
                    break;          
            }
        },
        close(){
            this.$uiHandler.closePopup();
        },
    },
};
</script>